.login {
background: #3f51b5;
width: 100%;
height: 100%;
}
.login .wrapBox {
    background: rgba(255, 255, 255, 0.7);
    border-radius: 10px;
    height: 406px;
    position: absolute;
    left: 50%;
    top: 30.56%;
    width: 388px;
    margin-left: -199px;
}
.login .innerBox {
    position: absolute;
    top: 8px;
    width: 404px;
    left: -8px;
    height: 426px;
    border-radius: 10px;
    padding: 40px;
}
.login .boxTitle {
    color: #e91e63;
    font-size: 24px;
    position: relative;
}
.login .boxTitle:before {
    background: #e91e63;
    content: '';
    position: absolute;
    top: 4px;
    left: -40px;
    width: 4px;
    height: 24px;
}
.login .absolute {
    flip: false;
    top: 56px;
    right: -30px;
    position: absolute;
    -webkit-box-shadow: 0px 7px 20px 0 rgba(233, 30, 99, 0.3);
            box-shadow: 0px 7px 20px 0 rgba(233, 30, 99, 0.3)
}


